@import url("https://cdnjs.cloudflare.com/ajax/libs/antd/4.21.5/antd.min.css");

@font-face {
    /* TODO: Font Family https://www.dfonts.org/assets/visby-round-font-family/ */
    font-family: 'VisbyRoundCF-Regular';
    src: local('VisbyRoundCF-Regular'), url(./assets/VisbyRoundCF-Regular.woff) format('woff');
    font-weight: normal;
}

@font-face {
    /* TODO: Font Family https://www.dfonts.org/assets/visby-round-font-family/ */
    font-family: 'VisbyRoundCF-DemiBold';
    src: local('VisbyRoundCF-DemiBold'), url(./assets/VisbyRoundCF-DemiBold.woff) format('woff');
    font-weight: normal;
}

@font-face {
    /* TODO: Font Family https://www.dfonts.org/assets/visby-round-font-family/ */
    font-family: 'VisbyRoundCF-Heavy';
    src: local('VisbyRoundCF-Heavy'), url(./assets/VisbyRoundCF-Heavy.woff) format('woff');
    font-weight: normal;
}
::-webkit-scrollbar {
    display:none;
  }

body {
    font-family: 'VisbyRoundCF-Regular';
    margin: 0px;
}

.form-title { 
    font-size: 42px; 
    font-family: 'VisbyRoundCF-Heavy'; 
    letter-spacing: 0.5px; 
    color: #e8e8e8; 
    padding-bottom: 12px; 
}

.form-subtitle {
    font-size: 18px;
    font-family: 'VisbyRoundCF-Regular';
    letter-spacing: 0.5px;
    color: #afafaf;
    padding-bottom: 24px;
}

.background-image {
    width: 100vw;
    height: 100vh;
    background-repeat: no-repeat;
    background-size: cover;
}

.background-gradient-dark {
    width: 100vw;
    height: 100%;
    overflow-y: scroll;
    background: linear-gradient(66deg, rgb(40,43,54) 0%, rgba(40,43,54,0.8) 100%);
    /* ^ Built with https://cssgradient.io/ */
}

.background-gradient-light {
    width: 100vw;
    height: 100%;
    overflow-y: scroll;
   /* background: linear-gradient(66deg, rgba(150, 157, 166, 0.9) 0%, rgba(150, 157, 166, 0.8) 50%, rgba(150,157,166,0.7) 100%);
     ^ Built with https://cssgradient.io/ */
}

.title{
   color:white;
   font-Size: "30px";
   font-family: "VisbyRoundCF-Heavy";
   letter-Spacing: "0.5px";
   padding-Bottom: "11vw";
}
.title-continer{
    position: absolute;
    left:24%;
    top:50px;
    height: 200px;
}
@media (min-width: 1500px) {
    .move {
      
       position: relative;
       top: 502px;
       right:593px;
       z-index: 100;
    }
}